@font-face {
  font-family: DS-DIGII;
  src: url(./DS-DIGII.TTF);
}

html {
  font-family: DS-DIGII, sans-serif;
  margin: 0;
  height: 100vh;
}

.App {
  display: grid;
  gap: 10px;
  justify-content: center;
  transform: translateY(30vh);
}

.Clock {
  font-size: 30px;
  margin: 10px auto;
}

.buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.button {
  padding: 10px;
  background-color: black;
  color: white;
  text-transform: uppercase;
  border: solid 1px black;
  cursor: pointer;

  &:active {
    background-color: rgb(66, 66, 66);
    border-color: rgb(66, 66, 66);
  }
}

.visible {
  visibility: visible;
}

.invisible {
  visibility: hidden;
}
